export const target_name = 'AUTO_SYNC::notebook';

export function get_cell_from_notebook(j: JupyterType, cell_number: number): Cell {
  let cell = j.notebook.get_cell(cell_number);

  while (cell === null) {
    // Kind of meh hack to just keep spamming cells at bottom until we get to the cell we want.
    j.notebook.insert_cell_at_bottom();

    cell = j.notebook.get_cell(cell_number);
  }

  return cell;
}

export function update_cell_contents(j: JupyterType, data: any): void {
  let cell = get_cell_from_notebook(j, data.cell_number);

  cell.set_text(data.cell_contents);
}

export function execute_cell_contents(j: JupyterType, data: any): void {
  let cell = get_cell_from_notebook(j, data.cell_number);

  cell.focus_cell();
  cell.execute();
}

export function create_and_register_comm(j: JupyterType): void {
  j.notebook.kernel.comm_manager.register_target(
    target_name,
    // comm is the frontend comm instance
    // msg is the comm_open message, which can carry data
    function(comm: any, _msg: any) {
      // Register handlers for later messages:
      comm.on_msg(function(msg: any) {
        if (msg.content.data.command === 'update') {
          update_cell_contents(j, msg.content.data);
        } else if (msg.content.data.command === 'execute') {
          execute_cell_contents(j, msg.content.data);
        } else {
          // debugger;
          console.log('Got an unexpected message: ', msg);
        }
      });

      comm.on_close(function(msg: any) {
        console.log('close', msg);
      });
    }
  );
}
